.loader-wrapper {
  min-height: 100px;
  position: relative; }

.loading {
  position: absolute;
  top: calc(50% - 10px);
  left: 50%; }
  .loading.relative {
    left: auto;
    padding: 25px;
    position: relative;
    text-align: center;
    top: auto;
    width: 100%; }

.loading-bar {
  display: inline-block;
  width: 4px;
  height: 20px;
  border-radius: 4px;
  animation: loading 1s ease-in-out infinite;
  margin-left: 2px; }

.loading-bar:nth-child(1) {
  animation-delay: 0;
  background-color: #368493;
  opacity: 1; }

.loading-bar:nth-child(2) {
  animation-delay: 0.09s;
  background-color: #368493;
  opacity: 0.80; }

.loading-bar:nth-child(3) {
  animation-delay: .18s;
  background-color: #368493;
  opacity: 0.60; }

.loading-bar:nth-child(4) {
  animation-delay: .27s;
  background-color: #368493;
  opacity: 0.40; }

@keyframes loading {
  0% {
    transform: scale(1); }
  20% {
    transform: scale(1, 2.2); }
  40% {
    transform: scale(1); } }
